<!-- Timeline.svelte -->
<script>
  import { onMount } from "svelte";
  import { Timeline, DataSet } from "vis-timeline/standalone";

  let timelineContainer;
  let timeline;

  // Example data for the timeline
  // const items = new DataSet([
  //   { id: 1, content: "Item 1", start: "2023-11-07" },
  //   { id: 2, content: "Item 2", start: "2023-11-08" },
  //   { id: 3, content: "Item 3", start: "2023-11-09", end: "2023-11-10" },
  // ]);

  const items = new DataSet([
    {
      id: 1,
      content: "Item 1",
      start: new Date(Date.UTC(2023, 10, 7)).getTime(),
      end: new Date(Date.UTC(2023, 10, 8)).getTime(),
    },
    {
      id: 2,
      content: "Item 2",
      start: new Date(Date.UTC(2023, 10, 8)).getTime(),
      end: new Date(Date.UTC(2023, 10, 9)).getTime(),
    },
    {
      id: 3,
      content: "Item 3",
      start: new Date(Date.UTC(2023, 10, 7)).getTime(),
      end: new Date(Date.UTC(2023, 10, 8)).getTime(),
    },
  ]);

  onMount(() => {
    timeline = new Timeline(timelineContainer, items, {
      start: new Date(Date.UTC(2023, 10, 7)).getTime(),
      end: new Date(Date.UTC(2023, 10, 10)).getTime(),
      timeZone: "utc", // Confirm UTC setting for the timeline instance
    });
  });
</script>

<div
  bind:this={timelineContainer}
  style="height: 400px; width: 100%;"
  class="vis-item"
></div>

<style>
  /* Optional: Add custom styling for the timeline */
  .vis-item {
    background-color: #4a90e2;
    color: white;
    border-radius: 4px;
    padding: 5px;
  }
</style>
